<template>
    <div>
        <Head title="注册" :show="true"></Head>
        <van-form @submit="registerSubmit">
            <van-field
                v-model="register.mobile"
                name="mobile"
                label="手机号"
                placeholder="手机号"
                :rules="[{ required: true, message: '请填写手机号码' }]"
            />
            <van-field
                v-model="register.username"
                name="username"
                label="用户名"
                placeholder="用户名"
                :rules="[{ required: true, message: '请填写用户名' }]"
            />
            <van-field
                v-model="register.password"
                type="password"
                name="password"
                label="密码"
                placeholder="密码"
                :rules="[{ required: true, message: '请填写密码' }]"
            />
            <van-field
                v-model="register.dbpwd"
                type="password"
                name="dbpwd"
                label="确认密码"
                placeholder="确认密码"
                :rules="[{ required: true, message: '请填写确认密码' }]"
            />
            <div style="margin: 16px;">
                <van-button round block type="primary" native-type="submit">
                马上注册
                </van-button>
            </div>
        </van-form>
    </div>
</template>
<script>
export default {
     data(){
        return {
            register:{},
           
        }
    },
    methods:{
        registerSubmit(){
            this.$axios.post("/vue/register",this.register)
            .then(res=>{
                this.$router.push({name:"login",query:{mobile:this.register.mobile}})
            })
        },
       
    }
}
</script>